
.tab {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 40px;
  line-height: 40px;
  position: relative;
}
.tab:before,
.tab:after {
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
  display: block;
  width: 100%;
  position: absolute;
  left: 0;
}
.tab:before {
  top: 0;
}
.tab:after {
  bottom: 0;
}
.tab .tab-item {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
}
.tab .tab-item > a {
  display: block;
  font-size: 14px;
  color: #4d555d;
}
.tab .tab-item > a.router-link-active {
  color: #f01414;
}


